import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Form, Col, Modal, Table } from 'antd';
import { TableData, SelectAjax, YearSemesterInput } from 'components/common/index';
import { getQueryParams } from 'utils/urlUtil';
import {
  getPolicyStudents,
  createPolicyStudents,
  deletePolicyStudents,
} from 'services/policyStudent';
import { getPolicyObjects } from 'services/policyObjectForPolicyStudent';
import { dateToString } from 'utils/formatUtil';
import { BtnSave, BtnCancel } from 'components/common/button/index';
import SearchBar from './SearchBar';
import './style.scss';

@Form.create({ name: 'formListPolicyObject' })
class ListPolicyObject extends Component {
  state = {
    reload: 0,
    paramSearch: {},
    visible: false,
    type: '',
    // enableButton: '',
    dataSearchTable: [],
    dataForSetPolicyStudents: [],
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  columns = [
    {
      title: 'Mã SV',
      dataIndex: 'code',
      key: 'code',
    },
    {
      title: 'Họ tên',
      dataIndex: 'fullname',
      key: 'fullname',
    },
    {
      title: 'Ngày sinh',
      dataIndex: 'dob',
      render: (text, record) => dateToString(record.dob, 'DD/MM/YYYY'),
    },
    {
      title: 'Lớp',
      dataIndex: 'className',
      key: 'className',
    },
    {
      title: 'Tên đối tượng ',
      dataIndex: 'policyObjectLabel',
      key: 'policyObjectLabel',
    },
  ];

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  showModal = type => {
    this.setState({
      visible: true,
      type,
    });
  };

  handleOk = e => {
    e.preventDefault();
    this.setState({
      visible: false,
    });

    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      const { dataForSetPolicyStudents } = this.state;
      const data = dataForSetPolicyStudents.map(x => {
        const { studentId } = x;
        return { studentId, ...values };
      });
      this.saveData(data);
    });
  };

  saveData = async data => {
    try {
      console.table(data);
      if (data.policyObjectId != null) {
        await createPolicyStudents(data);
      }
      await deletePolicyStudents(data);
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  handleCancel = e => {
    e.preventDefault();
    this.setState({
      visible: false,
    });
  };

  // showButtonCancelPolicy = () => {
  //   if (this.state.dataSearchTable.studentId == null) {
  //     return <BtnCancel onClick={() => this.showModal('unset')} title="Hủy đối tượng" disabled />;
  //   }
  //   return <BtnCancel onClick={() => this.showModal('unset')} title="Hủy đối tượng" />;
  // };

  render() {
    const rowSelectedOfDataSearchTable = {
      data: [],
      onChange: (selectedRowKeys, selectedRows) => {
        this.setState({ dataSearchTable: selectedRows });
        // if (this.state.dataSearchTable.policyObjectId == null) {
        //   this.setState({ enableButton: 'disabled' });
        // }

        // rowSelectedOfDataSearchTable.data = selectedRows;
        // console.table(rowSelectedOfDataSearchTable.data);
      },
    };

    const { getFieldDecorator } = this.props.form;
    const rowSelectedSetPolicyStudents = {
      onChange: (selectedRowKeys, selectedRows) => {
        this.setState({ dataForSetPolicyStudents: selectedRows });
      },
    };
    const { paramSearch = {} } = this.state;
    // const { form } = this.props;
    // form.setFieldsValue({ semesterId: paramSearch.semesterId });
    // form.setFieldsValue({ yearEduId: paramSearch.yearEduId });
    return (
      <Fragment>
        <Card title="Chọn đối tượng chính sách">
          <Row>
            <Col span={24}>
              <SearchBar />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <TableData
                rowSelection={rowSelectedOfDataSearchTable}
                reload={this.state.reload}
                paramSearch={this.state.paramSearch}
                allowGetData={
                  !!paramSearch.trainTypeId &&
                  !!paramSearch.facultyId &&
                  !!paramSearch.courseId &&
                  !!paramSearch.semesterId &&
                  !!paramSearch.yearEduId
                }
                service={getPolicyStudents}
                columns={this.columns}
                rowKey="id"
              />
            </Col>
            <Col span={24} style={{ textAlign: 'center' }}>
              <div className="btn-group">
                <BtnSave onClick={() => this.showModal('set')} title="Xét đối tượng" />
                {/* {() => this.showButtonCancelPolicy} */}
                <BtnCancel
                  onClick={() => this.showModal('unset')}
                  title="Hủy đối tượng"
                  // disabled={this.state.enableButton}
                />
              </div>
            </Col>
          </Row>
          <Modal
            style={{ top: 20 }}
            width="80%"
            title={this.state.type === 'set' ? 'Xét đối tượng' : 'Hủy đối tượng'}
            visible={this.state.visible}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
          >
            <Row>
              <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
                <Col span={24}>
                  <YearSemesterInput form={this.props.form} />
                  <Form.Item label="ĐTCS">
                    {getFieldDecorator(`policyObjectId`, {})(
                      <SelectAjax service={getPolicyObjects} />
                    )}
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <Table
                    rowSelection={rowSelectedSetPolicyStudents}
                    dataSource={this.state.dataSearchTable}
                    columns={this.columns}
                    rowKey="id"
                  />
                </Col>
              </Form>
            </Row>
          </Modal>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListPolicyObject />
  </Page>
);



